#nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  font-size: 1.3rem;

  color: white;
  background-color: white;

  z-index: 10;
}

.shoe-logo {
  width: 100px;
  height: 100px;

  transition: ease-in-out 0.2s;
  padding-left: 20px;
}

#nav-list {
  margin: 0;
  padding: 5px;

  display: flex;
  justify-content: space-around;
  align-items: center;

  list-style: none;
}

ul#nav-list a.nav-item {
  text-decoration: none;
  transition: ease-in-out 0.2s;

  margin-left: 2em;
  margin-right: 2em;
}

/*
#########################################
#########################################

    CART STYLING

#########################################
######################################### 
*/

.cart-nav-container {
  position: relative;

  background-color: rgb(202, 62, 62);
  border-radius: 50%;

  margin-right: 20px;
  margin-left: 2em;
}

.cart-image {
  width: 40px;
  height: 40px;

  padding: 5px;

  transition: ease-in-out 0.2s;
}

.cart-counter {
  position: absolute;
  bottom: -5px;
  left: -5px;
  color: black;
  font-weight: 800;

  text-align: center;

  width: 25px;
  height: 25px;

  background-color: white;
  border: 1px solid black;
  border-radius: 50%;
}

.animate {
  animation: appear ease-in 1s;
}

@keyframes appear {
  0% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

/* Style The Dropdown Button */
/* Source: https://www.w3schools.com/css/css_dropdowns.asp*/
.dropbtn {
  background-color: rgb(202, 62, 62);
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;

  margin-right: 20px;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  margin-right: 20px;

  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 3;
}

.dropdown-content a span {
  color: black;
  padding: 12px 16px;
  display: block;
}

a {
  text-decoration: none;
}

@media (min-width: 650px) {
  .cart-nav-container:hover img {
    cursor: pointer;
  
    transform: skewY(-15deg);
    transform: skewX(-15deg);
  }
  ul#nav-list a.nav-item:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
  .shoe-logo:hover {
    cursor: pointer;
    transform: translateY(-5px);
  }
}